.imgHover {
  perspective: 1000px;
  border-radius: 50%;
}
/* .imgHover:hover {
  transform: rotateY(45deg);
  transform-origin: bottom;
  transform-style: preserve-3d;
  transition: all 0.5s;
} */
.imgHover::after {
  content: "";
  position: relative;
  left: 0;
  bottom: 0;
  background: inherit;
  background-size: cover, cover;
  background-position: bottom;
  transform: rotateX(90deg);
  transform-origin: bottom;
}
